import React from "react"

function Tags(props){
    const {content, randomColors, glow} = props
    let contentTags = []
    console.log('glow',glow);
    for(let i=0; i<content.length; i++){
      let text = content[i]
      let randomColor = randomColors[Math.floor(Math.random()*randomColors.length)]
      let boxShadow = (glow === undefined || glow === true) ? `0 0 10px ${randomColor}` : "none"
      contentTags.push(<div className="tag" style={{ color: randomColor, borderColor: randomColor, boxShadow: boxShadow }}>{text}</div>)
    }
    return contentTags
}

export default Tags